<template lang="">
    <div>
        <div class="container-fluid about-banner">
            <div class="container">
                <div class="row justify-center align-center text-center content-2">
                    <div class="col-lg-6 col-md-8 text-light">
                        <h2><b>{{ card.title }}</b></h2>
                        <p>{{ card.subheading }}</p>
                    </div>
                </div>
            </div>
        </div>

        <div class="container py-5">
            <div class="row justify-content-center align-items-stretch">
                <h3 class="py-4 text-center"><b>Please fill the details to receive download link</b></h3>
                <div class="col-lg-7 col-md-12 col-sm-12 col-12 p-5 d-flex contact-card align-items-stretch">
                    <div class="text-light align-items-center">
                        <h3 class="text-center">{{ card.form_header }}</h3>

                        <div class="col-lg-12 py-3">
                            <p>{{ card.form_subheading }}</p>
                            <p>{{ card.form_subheading2 }}</p>
                        </div>

                        <img :src="'/' + card.image" style="max-width:100%" v-if="card.image" />
                    </div>
                </div>
                <div class="col-lg-5 contact-form">
                    <form class="row g-3 p-2" name="fromPublication" @submit.prevent="submit">
                        <div class="col-md-12">
                            <label for="" class="form-label">Name</label>
                            <input type="text" class="form-control" name="name" v-model="form.name" required>
                        </div>
                        <div class="col-md-12">
                            <label for="" class="form-label">Email</label>
                            <input type="email" class="form-control" name="email" v-model="form.email" required>
                        </div>
                        <div class="col-12">
                            <label for="" class="form-label">Phone</label>
                            <input type="number" class="form-control" name="phone" v-model="form.phone">
                        </div>
                        <div class="col-12">
                            <label for="" class="form-label">Company</label>
                            <input type="text" class="form-control" name="company" v-model="form.company">
                        </div>
                        <div class="mb-3">
                            <input type="checkbox" id="agree" name="agree" v-model="form.agree">
                            <label for="agree" style="display:inline;margin-left: 5px;">By filling and submitting this form, you understand and agree that the use of ValueXPA website and its contents is subject to the <router-link :to="{name: 'terms'}" target="_blank">Terms and Conditions</router-link>. Additional details regarding our collection and use of your personal information can be seen in <router-link :to="{name: 'privacy'}" target="_blank">Privacy Policy</router-link>.</label>
                        </div>
                        <div class="col-12">
                            <button type="submit" class="btn btn-secondary-2">Send</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>

        <Footer/>
    </div>
</template>
<script>
import Footer from "../components/Footer.vue"
import axios from "axios"
export default {
    components:{
        Footer,
    },
    data() {
        return {
            form: {
                name: null,
                email: null,
                phone: null,
                company: null,
                agree: null,
                publication_id: null
            },
            card: []
        }
    },
    methods: {
        async loadData() {
            let slug = this.$route.params.slug;

            axios.get("/api/publications/" + slug).then((res) => {
                this.card = res.data;
                this.form.publication_id = res.data.id;
            });
        },
        submit()
        {
            axios.post('/api/form/publication', this.form)
            .then((data) => {
                alert('Details successfully submitted');
                document.forms.fromPublication.reset();
            })
            .catch(function (error) {
                if ( error.response.data.errors )
                {
                    let errorMsg = error.response.data.message;
                    for (let prop in error.response.data.errors) {
                        errorMsg += "\n" + prop + ": " + error.response.data.errors[prop];
                    }
                    alert(errorMsg);
                }
            });
        },
    },
    mounted() {
        this.loadData();
    }
}
</script>
